<template>
    <div class="content-bg">
      <div class="top-title">广播包/组播包
        <v-flowchartfrom class="from-wapper" refreshBus="mulAndBroRefreshBus" :refreshObj="refreshObj"></v-flowchartfrom>
      </div>
      <div class="item-wapper">
        <div class="item-title">全局-广播包、组播包</div>
        <div class="item-content">
          <v-echarts echartType="timeline" :echartData = "this.allMulAndBroData" ></v-echarts>
        </div>
      </div>
    </div>
    
</template>

<script>
    import vFlowchartfrom from '../common/flowChartsFrom'
    import vEcharts from '../common/echarts'
    import bus from '../common/bus';
    import {dateFormat} from '../../../static/js/common'
    export default {
        name: "mulAndBro",
        data() {
            return {
                interTime:'',
                refreshObj:{
                  defaultVal:'5',
                  opt:[{
                    label:'5s',
                    value:'5'
                  },
                    {
                      label:'10s',
                      value:'10'
                    },
                    {
                      label:'20s',
                      value:'20'
                    }
                  ]
                },
                //刷新时间间隔
                refreshIntTime :'5000',
                //数据日期间隔
                dataTime:1,
                //全局广播包组播包个数
                allMulAndBroData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'时间',
                        yAxisName:'个数',
                        hoverText:'',
                    },
                    yAxisArr:[{
                        name:'组播包',
                        color:'rgb(15,219,243)',
                        data:[]
                    },{
                        name:'广播包',
                        color:'rgb(9,243,105)',
                        data:[]
                    }]
                }
            }
        },
        created(){
            /*监听刷新时间改变*/
            bus.$on('mulAndBroRefreshBus',(val)=>{
                    this.refreshIntTime = val*1000;
                    clearInterval(this.interTime);
                    this.setInterFun(this.refreshIntTime/1000)
            })
        },
        mounted(){
            /*获取组播包广播包个数*/
            this.getAllMulAndBroData(this.refreshIntTime/1000);
        },
        methods:{
            /*获取广播包组播包个数*/
            getAllMulAndBroData(timeInterval){
                this.$nextTick(()=> {
                    this.$post('/flow/getMulticastAndBroadcastPacketTypeCount.do', {
                      timeInterval:timeInterval
                    })
                        .then(res => {
                            if (this.allMulAndBroData.yAxisArr[0].data.length < 60) {
                                this.allMulAndBroData.yAxisArr[0].data.push(res[0].multicast);
                                this.allMulAndBroData.yAxisArr[1].data.push(res[0].broadcast);
                            } else {
                                this.allMulAndBroData.yAxisArr[0].data.shift();
                                this.allMulAndBroData.yAxisArr[0].data.push(res[0].multicast);
                                this.allMulAndBroData.yAxisArr[1].data.push(res[0].broadcast);
                            }
                        })
                        .catch(err => {

                        })
                })
            },
          /*计时器*/
          setInterFun(){
            this.interTime = setInterval(()=>{
              /*获取全局利用率百分比*/
              this.getAllMulAndBroData(this.refreshIntTime/1000);
            },this.refreshIntTime);
          }
        },
      beforeRouteEnter(to, from, next){
        next(vm => {
          vm.setInterFun();
        });
      },
        beforeRouteLeave(to, from, next) {
          clearInterval(this.interTime);
          next();
        },
        components:{
            vFlowchartfrom,
            vEcharts
        }
    }
</script>

<style scoped>
  .content-bg{
    /*height: 1000px;*/
    font-size: 1rem;
    background: url("../../../static/img/flow-index-bg2.png");
    background-size: 100% 100%;
  }
  .top-title{
    font-size: 0.5rem;
    color: #185bff;
    padding: 10px;
  }
  .item-wapper{
    height: auto;
    /*background: rgb(26,36,47);*/
    background: url("../../../static/img/bd2.png");
    background-size: 100% 100%;
    margin:0.5rem 0.2rem;
  }
  .item-title{
    height: 1rem;
    line-height: 1rem;
    font-size: 0.38rem;
    text-align: left;
    padding-left: 25px;
    /*background: url("../../../static/img/title-bg.png");*/
    background-size: 100% 100%;
  }
  .item-content{
    /*height: calc(100% - 1rem);*/
    height: 230px;
    box-sizing: border-box;
    padding: 0.5rem;
    padding-top: 0;
  }
</style>



